.postContainer {
	max-height: 520px;
	max-width: 320px;
	border: 2px solid var(--primary-blue);
	border-radius: 4px;
	box-shadow: 3px 2px 10px 1px var(--opac-black);
	overflow: hidden;
	transition: 0.2s all ease-in-out;
}

.postContainer:hover {
	transform: scale(1.02);
	box-shadow: 8px 5px 10px 2px var(--opac-black);
}